/**
 * @file ERA  - 
 * @description
 *  `<VariableEdit>`  `EditLog`
 *
 * ****:
 * - ****: 
 * - ****: `EditLog` `applyEditAtLevel` 
 *   
 * - ****: 
 *   
 */
'use strict';

import { sanitizeArrays } from '../../utils/data';
import { updateEraStatData } from '../../utils/era_data';
import { Logger } from '../../utils/log';
import { findLatestNewValue } from '../rollback';

const logger = new Logger();

/**
 * ****
 *  `<VariableEdit>` ****
 *
 * ****:
 * 1. ****
 * 2. ****
 * 3. **** `EditLog`****
 *     `findLatestNewValue`  `EditLog` 
 *    
 *
 * @param {any} statData -  ( `stat_data`)
 * @param {string} basePath - 
 * @param {any} patchObj - 
 * @param {any[]} editLog - 
 * @param {number} messageId -  ID
 * @param {Map<string, any>} intraMessageState - ****
 */
export async function applyEditAtLevel(
  statData: any,
  basePath: string,
  patchObj: any,
  editLog: any[],
  messageId: number,
  intraMessageState: Map<string, any>,
) {
  // --- 1.  ---
  const currentNodeInVars = basePath ? _.get(statData, basePath) : statData;
  if (currentNodeInVars === undefined) {
    logger.warn('applyEditAtLevel', `VariableEdit  -> ${basePath || '(root)'}`);
    return;
  }

  // --- 2.  (`updatable`) ---
  //  updatable  true ()
  const isUpdatable = _.get(currentNodeInVars, ['$meta', 'updatable'], true);

  //  updatable  false  true 
  const isBypassingProtection =
    isUpdatable === false && // 
    _.get(patchObj, ['$meta', 'updatable']) === true; //  true

  // 
  if (isUpdatable === false && !isBypassingProtection) {
    logger.warn(
      'applyEditAtLevel',
      `VariableEdit  <${basePath}>  "$meta.updatable: false" `,
    );
    return; // 
  }

  // --- 3.  ---
  // 
  for (const key of Object.keys(patchObj)) {
    const subPath = basePath ? `${basePath}.${key}` : key;
    const valNew = patchObj[key];

    // ****
    // 
    if (_.isPlainObject(valNew)) {
      await applyEditAtLevel(statData, subPath, valNew, editLog, messageId, intraMessageState);
      continue; // 
    }

    // ** ()**
    // 

    // 
    if (!_.has(statData, subPath)) {
      logger.warn('applyEditAtLevel', `VariableEdit  -> ${subPath}`);
      continue;
    }

    // a.  (`valOld`)
    // 
    logger.debug('applyEditAtLevel', `[]  <${subPath}>  ID <${messageId}> ...`);
    let valOld = await findLatestNewValue(subPath, messageId);
    if (valOld === null) {
      valOld = _.get(statData, subPath);
      logger.debug(
        'applyEditAtLevel',
        `[]  stat_data : ${JSON.stringify(valOld)}`,
      );
    } else {
      logger.debug('applyEditAtLevel', `[] : ${JSON.stringify(valOld)}`);
    }

    const cleaned = sanitizeArrays(valNew); // 

    // b. 
    //  EditLog
    // 
    _.set(statData, subPath, cleaned);
    editLog.push({
      op: 'update',
      path: subPath,
      value_old: _.cloneDeep(valOld),
      value_new: _.cloneDeep(cleaned),
    });
    // c.  Map
    intraMessageState.set(subPath, _.cloneDeep(cleaned));
  }
}

/**
 *  `<VariableEdit>` 
 *
 * @param {any[]} allEdits -  edit 
 * @param {any[]} editLog - 
 * @param {number} messageId -  ID
 */
export async function processEditBlocks(allEdits: any[], editLog: any[], messageId: number) {
  if (allEdits.length > 0) {
    const intraMessageState = new Map<string, any>(); // ****
    // N.B. 
    for (const editRoot of allEdits) {
      if (!_.isPlainObject(editRoot) || _.isEmpty(editRoot)) continue;
      try {
        await updateEraStatData(async stat => {
          logger.debug('processEditBlocks', ` editRoot: ${JSON.stringify(editRoot)}`);
          //  '' 
          await applyEditAtLevel(stat, '', editRoot, editLog, messageId, intraMessageState);
          return stat;
        });
      } catch (e: any) {
        logger.error('processEditBlocks', ` editRoot : ${e?.message || e}`, e);
      }
    }
    logger.log('processEditBlocks', ' VariableEdit ');
  }
}
