/**
 * @file ERA  - ERA 
 */

'use strict';

import _ from 'lodash';
import { CHAT_SCOPE, META_DATA_PATH, STAT_DATA_PATH, SettingsSchema } from './constants';

declare function getScriptId(): string;

/**
 *  `$`  `$meta`, `$template`
 * 
 * @param {any} obj - 
 * @returns {any}  `$` 
 */
export function removeMetaFields(obj: any): any {
  // 
  if (!_.isObject(obj)) {
    return obj;
  }

  // 
  const newObj = _.cloneDeep(obj);

  function recurse(current: any) {
    if (Array.isArray(current)) {
      // 
      current.forEach(item => recurse(item));
    } else if (_.isPlainObject(current)) {
      // 
      for (const key in current) {
        //  '$' 
        if (key.startsWith('$')) {
          delete current[key];
        } else {
          // 
          recurse(current[key]);
        }
      }
    }
  }

  recurse(newObj);
  return newObj;
}

/**
 *  ERA 
 * @returns {{meta: object, stat: object}} 
 */
export function getEraData(): { meta: any; stat: any } {
  const chatVars = getVariables(CHAT_SCOPE) || {};
  const meta = _.get(chatVars, META_DATA_PATH, {});
  const stat = _.get(chatVars, STAT_DATA_PATH, {});
  return { meta, stat };
}

/**
 *  ERA  (stat_data)
 * @param {(currentStatData: any) => (any | Promise<any>)} updater -  stat_data  stat_data  ( async)
 */
export async function updateEraStatData(updater: (currentStatData: any) => any | Promise<any>) {
  await updateVariablesWith(async v => {
    const currentStat = _.get(v, STAT_DATA_PATH, {});
    const newStat = await updater(currentStat);
    _.set(v, STAT_DATA_PATH, newStat);
    return v;
  }, CHAT_SCOPE);
}

/**
 *  ERA  (ERAMetaData)
 * @param {(currentMetaData: any) => (any | Promise<any>)} updater -  ERAMetaData  ERAMetaData  ( async)
 */
export async function updateEraMetaData(updater: (currentMetaData: any) => any | Promise<any>) {
  await updateVariablesWith(async v => {
    const currentMeta = _.get(v, META_DATA_PATH, {});
    const newMeta = await updater(currentMeta);
    _.set(v, META_DATA_PATH, newMeta);
    return v;
  }, CHAT_SCOPE);
}

/**
 * 
 * @returns {z.infer<typeof SettingsSchema>}
 */
export function getScriptSettings(): z.infer<typeof SettingsSchema> {
  const rawSettings = getVariables({ type: 'script', script_id: getScriptId() });
  return SettingsSchema.parse(rawSettings ?? {});
}

/**
 * 
 * @param {(currentSettings: z.infer<typeof SettingsSchema>) => z.infer<typeof SettingsSchema>} updater
 * @returns {Promise<void>}
 */
export async function updateScriptSettings(
  updater: (
    currentSettings: z.infer<typeof SettingsSchema>,
  ) => z.infer<typeof SettingsSchema> | Promise<z.infer<typeof SettingsSchema>>,
): Promise<void> {
  await updateVariablesWith(
    async rawSettings => {
      const currentSettings = SettingsSchema.parse(rawSettings ?? {});
      const newSettings = await updater(currentSettings);
      return newSettings;
    },
    { type: 'script', script_id: getScriptId() },
  );
}
