/**
 * @file ERA  - 
 * @description
 *  ERA  `EditLog` 
 * 
 *
 * ****:
 * - ****: `rollbackByMk`  `EditLog`
 *   `insert` -> `unset`, `update` -> `set to value_old`
 * - ****: `EditLog` `findLatestNewValue` 
 *    `EditLog` 
 *   
 */

'use strict';

import { CHAT_SCOPE, LOGS_PATH, META_DATA_PATH, STAT_DATA_PATH } from '../utils/constants';
import { J, parseEditLog } from '../utils/data';
import { getEraData } from '../utils/era_data';
import { Logger } from '../utils/log';
import { getMessageContent, isUserMessage } from '../utils/message';
import { readMessageKey } from './key/mk';

const logger = new Logger();

/**
 * ****
 * MK
 *
 * @param {string} MK - 
 * @param {boolean} [silent=false] - 
 *    `resyncStateOnHistoryChange` 
 */
export async function rollbackByMk(MK: string, silent = false) {
  try {
    logger.log('rollbackByMk', `, MK=${MK}`);
    await updateVariablesWith(v => {
      const meta = _.get(v, META_DATA_PATH, {});
      const stat = _.get(v, STAT_DATA_PATH, {});

      const raw = _.get(meta, [LOGS_PATH, MK]);
      const arr = parseEditLog(raw);
      if (!arr || !arr.length) {
        logger.debug('rollbackByMk', `EditLog `);
        return v;
      }

      //  EditLog 
      // 
      for (let i = arr.length - 1; i >= 0; i--) {
        const e = arr[i];
        const op = String(e?.op || '').toLowerCase();
        const path = String(e?.path || '');
        if (!path) continue;

        if (op === 'insert') {
          // 
          _.unset(stat, path);
          continue;
        }
        if (op === 'update' || op === 'delete') {
          // 
          if (typeof e?.value_old === 'undefined') {
            // 
            _.unset(stat, path);
          } else {
            _.set(stat, path, _.cloneDeep(e.value_old));
          }
        }
      }

      _.set(v, STAT_DATA_PATH, stat);
      return v;
    }, CHAT_SCOPE);
    logger.log('rollbackByMk', `MK=${MK}`);
  } catch (e: any) {
    logger.error('rollbackByMk', `MK=${MK}  ${e?.message || e}`, e);
  }
}

/**
 * ****
 *  (`value_new`)
 *  `applyEditAtLevel`  `update` 
 *  `value_old`
 *
 * @param {string} path - 
 * @param {number} startMessageId -  ID ****
 * @returns {Promise<any>}  `value_new` `null`
 */
export async function findLatestNewValue(path: string, startMessageId: number): Promise<any> {
  logger?.debug('findLatestNewValue', ` <${path}> ID <${startMessageId}> ...`);
  const messages = getChatMessages('0-{{lastMessageId}}', { include_swipes: false });
  if (!messages || messages.length < 1) {
    logger?.debug('findLatestNewValue', ``);
    return null;
  }

  const startIndex = messages.findIndex(m => m.message_id === startMessageId);
  if (startIndex === -1) {
    logger?.warn('findLatestNewValue', `ID: ${startMessageId}`);
    return null;
  }

  // 
  for (let i = startIndex - 1; i >= 0; i--) {
    const message = messages[i];
    const msgId = message?.message_id;
    logger?.debug(
      'findLatestNewValue',
      `[]  (ID: ${msgId}): "${(getMessageContent(message) || '').substring(0, 100)}..."`,
    );

    //  isUserMessage  message_id
    if (isUserMessage(message) || typeof msgId !== 'number') {
      continue;
    }

    const mk = readMessageKey(message);
    if (!mk) {
      logger?.debug('findLatestNewValue', `[]  (ID: ${msgId})  MK`);
      continue; //  MK  AI 
    }

    const { meta: metaData } = getEraData();
    const editLogRaw = _.get(metaData, [LOGS_PATH, mk]);
    const editLog = parseEditLog(editLogRaw);

    if (!editLog || editLog.length === 0) {
      logger?.debug('findLatestNewValue', `[] MK ${mk}  EditLog `);
      continue; //  EditLog 
    }
    logger?.debug('findLatestNewValue', `[]  MK ${mk}  EditLog...\n${J(editLog)}`);

    //  EditLog
    for (let j = editLog.length - 1; j >= 0; j--) {
      const logEntry = editLog[j];
      if (!logEntry || !logEntry.path) continue;

      // Case 1: 
      if (logEntry.path === path) {
        //  delete 
        //  applyEditAtLevel 
        //  null
        if (logEntry.op === 'delete') {
          logger?.error(
            'findLatestNewValue',
            `>> ! (ID:${message.message_id}, MK:${mk}) <${path}>  'delete'  update `,
          );
          return null;
        }

        logger?.debug(
          'findLatestNewValue',
          `>> ! (ID:${message.message_id}, MK:${mk}) <${path}> : ${J(logEntry.value_new)}`,
        );
        return _.cloneDeep(logEntry.value_new);
      }

      // Case 2:  ( logEntry.path )
      // ,  "a.b.c",  "a.b" 
      if (path.startsWith(logEntry.path + '.')) {
        const subPath = path.substring(logEntry.path.length + 1);
        const parentNewVal = logEntry.value_new;
        if (_.isPlainObject(parentNewVal) && _.has(parentNewVal, subPath)) {
          const foundVal = _.get(parentNewVal, subPath);
          logger?.debug(
            'findLatestNewValue',
            `>> ! (ID:${message.message_id}, MK:${mk}) <${logEntry.path}>,  <${subPath}> : ${J(foundVal)}`,
          );
          return _.cloneDeep(foundVal);
        }
      }
    }
  }

  // 
  logger?.debug('findLatestNewValue', ` ${path}  null `);
  return null;
}

