/**
 * @file ERA  - 
 * @description
 *  ERA  `chat` 
 *
 * ****:
 * 
 * 
 * 1. ****:  AI  `<VariableInsert>`  `<VariableEdit>` 
 * 2. ****: 
 * 3. ****:  `recursive.ts`  `EditLog`
 * 4. ****: MK `EditLog` 
 *     `swipe` 
 *
 * ****:
 * - ** MK** MK  `message_key.ts` 
 * - `ApplyVarChangeForMessage` ** `EditLog`** `SelectedMks`
 *    `SelectedMks`  `ApplyVarChange` 
 *   
 */

'use strict';

import { readMessageKey } from '../../core/key/mk';
import { LOGS_PATH, SEL_PATH } from '../../utils/constants';
import { escapeEraData, parseEditLog, parseJsonl } from '../../utils/data';
import { updateEraMetaData } from '../../utils/era_data';
import { Logger } from '../../utils/log';
import { findLastAiMessage, getMessageContent, isUserMessage } from '../../utils/message';
import { createTagRegex, extractValidBlocks } from '../../utils/string';
import { processDeleteBlocks } from './delete';
import { processInsertBlocks } from './insert/insert';
import { processEditBlocks } from './update';

const logger = new Logger();

/**
 * **** 
 * 
 *
 * @param {TavernMessage} msg - 
 * @returns {Promise<string | null>}  MK null
 */
export const ApplyVarChangeForMessage = async (msg: any): Promise<string | null> => {
  logger.debug('ApplyVarChangeForMessage', `...`, { msg });
  try {
    if (!msg || typeof msg.message_id !== 'number') {
      logger.warn('ApplyVarChangeForMessage', ' message_id');
      return null;
    }

    const messageId = msg.message_id;
    //  MK `ensureMessageKey` 
    const MK = readMessageKey(msg);

    //  MK MK
    if (!MK) {
      logger.debug('ApplyVarChangeForMessage', ` (ID: ${messageId})  MK`);
      return null;
    }

    //  MK
    if (isUserMessage(msg)) {
      logger.debug('ApplyVarChangeForMessage', ` (ID: ${messageId})  MK`);
      return MK;
    }

    const rawContent = getMessageContent(msg) || '';

    // 1. 
    const insertBlocks = extractValidBlocks(rawContent, createTagRegex('VariableInsert', 'exact'));
    const editBlocks = extractValidBlocks(rawContent, createTagRegex('VariableEdit', 'exact'));
    const deleteBlocks = extractValidBlocks(rawContent, createTagRegex('VariableDelete', 'exact'));

    // 
    logger.debug('ApplyVarChangeForMessage', 'delete', deleteBlocks);

    if (!insertBlocks.length && !editBlocks.length && !deleteBlocks.length) {
      logger.debug('ApplyVarChangeForMessage', ` (ID: ${messageId}) `);
    }

    const rawInserts = insertBlocks.flatMap((s: string) => parseJsonl(s));
    const rawEdits = editBlocks.flatMap((s: string) => parseJsonl(s));
    const rawDeletes = deleteBlocks.flatMap((s: string) => parseJsonl(s));

    //  JSONL 
    //logger.debug('ERAJSONL', rawInserts);

    // 
    const allInserts = escapeEraData(rawInserts);
    const allEdits = escapeEraData(rawEdits);
    const allDeletes = escapeEraData(rawDeletes);

    logger.debug('ApplyVarChangeForMessage', '', {
      before: { inserts: rawInserts, edits: rawEdits, deletes: rawDeletes },
      after: { inserts: allInserts, edits: allEdits, deletes: allDeletes },
    });

    const editLog: any[] = []; // 

    // 2. ---  (`<VariableInsert>`) ---
    await processInsertBlocks(allInserts, editLog);

    // 3. ---  (`<VariableEdit>`) ---
    await processEditBlocks(allEdits, editLog, messageId);

    // 4. ---  (`<VariableDelete>`) ---
    await processDeleteBlocks(allDeletes, editLog);

    // 5. ---  EditLog ---
    /*
     *  editLog ()  EditLog
     *
     * 
     *
     *  MK  EditLog****
     *
     * 
     * 1. A (MK_A)  EditLog_A
     * 2. A swipeB (MK_B)
     *
     * B MK_B  EditLog 
     * 
     *
     *  swipeBAA
     *  MK_A  EditLog  EditLog_A
     *
     *  MK 
     * ****
     *  `swipe`
     *
     *  editLog ( `[]`) 
     * 
     */
    try {
      await updateEraMetaData(meta => {
        const newArr = Array.isArray(editLog) ? editLog : parseEditLog(editLog);
        logger.debug(
          'ApplyVarChangeForMessage',
          ` MK=${MK} (MsgID=${messageId})  EditLog:\n${JSON.stringify(newArr, null, 2)}`,
        );
        //  MK  `EditLogs` 
        _.set(meta, [LOGS_PATH, MK], JSON.stringify(newArr));
        /*
         * N.B.  SelectedMks 
         *  SelectedMks  (resyncStateOnHistoryChange  ApplyVarChange)
         *  resync  oldSelectedMks 
         */
        return meta;
      });
      logger.debug('ApplyVarChangeForMessage', ` MK=${MK}  EditLog`);
    } catch (e: any) {
      logger.error('ApplyVarChangeForMessage', ` MK=${MK}  EditLogs : ${e?.message || e}`, e);
    }

    return MK;
  } catch (err: any) {
    logger.error('ApplyVarChangeForMessage', `: ${err?.message || err}`, err);
    return null;
  }
};

/**
 * ****
 *  AI  `SelectedMks` 
 *  AI 
 */
export const ApplyVarChange = async () => {
  logger.debug('ApplyVarChange', `...`);
  // 1.  AI 
  const msg = findLastAiMessage();
  if (!msg || typeof msg.message_id !== 'number') {
    logger.log('ApplyVarChange', ' AI ');
    return;
  }

  const messageId = msg.message_id;
  logger.log('ApplyVarChange', ` AI  (ID: ${messageId})...`);

  // 2.  EditLog 
  // EditLog  MK 
  const MK = await ApplyVarChangeForMessage(msg);

  // 3.  SelectedMks
  try {
    await updateEraMetaData(meta => {
      const selectedMks = _.get(meta, SEL_PATH, []);
      //  AI  messageId 
      //  SelectedMks  MK 
      selectedMks[messageId] = MK;
      _.set(meta, SEL_PATH, selectedMks);
      return meta;
    });
  } catch (e: any) {
    logger.error('ApplyVarChange', ` SelectedMks : ${e?.message || e}`, e);
  }
};
