import { LOGS_PATH, SEL_PATH } from '../utils/constants';
import { parseEditLog } from '../utils/data';
import { getEraData, updateEraMetaData } from '../utils/era_data';
import { Logger } from '../utils/log';
import { ApplyVarChangeForMessage } from './crud/patcher';
import { readMessageKey } from './key/mk';
import { rollbackByMk } from './rollback';

const logger = new Logger();

/**
 * MKnull
 * @param msg 
 * @returns MK  null
 */
const getMkFromMsg = (msg: any): string | null => {
  const key = readMessageKey(msg);
  if (!key) return null;

  return key;
};

/**
 *  MK  EditLog 
 * @param mks MK 
 * @returns  EditLog  true
 */
const checkEditLogsAreEmpty = (mks: (string | null)[]): boolean => {
  const { meta: metaData } = getEraData();
  for (const mk of mks) {
    if (!mk) continue; //  null ()
    const editLogRaw = _.get(metaData, [LOGS_PATH, mk]);
    const editLog = parseEditLog(editLogRaw);
    if (editLog.length > 0) {
      return false; //  log false
    }
  }
  return true; //  log 
};

/**
 * 
 * 
 * 
 * @param {boolean} [forceFullResync=false] -  true
 */
export const resyncStateOnHistoryChange = async (forceFullResync = false) => {
  if (forceFullResync) {
    logger.warn('resyncStateOnHistoryChange', '');
  } else {
    logger.log('resyncStateOnHistoryChange', '...');
  }

  // getChatMessages  message_id 0 
  const allMessages = getChatMessages('0-{{lastMessageId}}', { include_swipes: true });
  logger.debug('resyncStateOnHistoryChange', ' allMessages:', allMessages);
  const { meta: oldMetaData } = getEraData();
  const oldSelectedMks: (string | null)[] = _.cloneDeep(_.get(oldMetaData, SEL_PATH, []));

  logger.debug(
    'resyncStateOnHistoryChange',
    `: oldSelectedMks.length=${oldSelectedMks.length}, allMessages.length=${allMessages?.length ?? 0}`,
  );

  if (!allMessages || allMessages.length === 0) {
    logger.log('resyncStateOnHistoryChange', '');
    return;
  }

  let firstRecalcId = -1;

  if (forceFullResync) {
    firstRecalcId = 0;
    logger.log('resyncStateOnHistoryChange', ' 0');
  }
  // Case 1:  ()
  else if (allMessages.length < oldSelectedMks.length) {
    logger.log('resyncStateOnHistoryChange', '');
    //  "currentMk === oldMk_at_same_index" 
    for (let i = allMessages.length - 1; i >= 0; i--) {
      const currentMk = getMkFromMsg(allMessages[i]);
      const recordedMk = oldSelectedMks[i];
      logger.debug(
        'resyncStateOnHistoryChange',
        `[-] i=${i}, currentMk=${currentMk}, recordedMk=${recordedMk}`,
      );

      if (currentMk === recordedMk) {
        firstRecalcId = i + 1;
        logger.log(
          'resyncStateOnHistoryChange',
          ` message_id=${i} (MK=${currentMk}) ID ${firstRecalcId} `,
        );
        break;
      }
    }
    if (firstRecalcId === -1) {
      firstRecalcId = 0;
      logger.log('resyncStateOnHistoryChange', '');
    }

    //  MK 
    //  MK MK 
    const currentMkSequence = allMessages.map(getMkFromMsg).filter(mk => mk);
    const oldMkSequence = oldSelectedMks.filter(mk => mk);
    const deletedMks = _.difference(oldMkSequence, currentMkSequence);

    logger.debug('resyncStateOnHistoryChange', `MK: [${oldMkSequence.join(', ')}]`);
    logger.debug('resyncStateOnHistoryChange', `MK: [${currentMkSequence.join(', ')}]`);
    logger.debug('resyncStateOnHistoryChange', `MK: [${deletedMks.join(', ')}]`);

    if (deletedMks.length > 0 && checkEditLogsAreEmpty(deletedMks)) {
      logger.log(
        'resyncStateOnHistoryChange',
        ` ${deletedMks.length} `,
      );
      const newSelectedMks: (string | null)[] = [];
      for (let i = 0; i < allMessages.length; i++) {
        newSelectedMks[i] = getMkFromMsg(allMessages[i]);
      }
      await updateEraMetaData(meta => {
        _.set(meta, SEL_PATH, newSelectedMks);
        return meta;
      });
      logger.log('resyncStateOnHistoryChange', ' SelectedMks ');
      return;
    }
  }
  // Case 2: / ()
  else if (allMessages.length === oldSelectedMks.length) {
    logger.log('resyncStateOnHistoryChange', '');
    // 
    for (let i = allMessages.length - 1; i >= 0; i--) {
      const currentMk = getMkFromMsg(allMessages[i]);
      const recordedMk = oldSelectedMks[i];
      logger.debug(
        'resyncStateOnHistoryChange',
        `[-] i=${i}, currentMk=${currentMk}, recordedMk=${recordedMk}`,
      );
      if (currentMk !== recordedMk) {
        firstRecalcId = i;
      }
    }
    if (firstRecalcId === -1) {
      // N.B. MK 
      //  swipe MK 
      //  MK 
      // logger.log('resyncStateOnHistoryChange', 'MK');
      // return; // 

      // 
      //  MK 
      // 
      //
      //  Bug selectedMK  editLog 
      // 
      // selectedMks, editLogs
      // 
      firstRecalcId = allMessages.length > 0 ? allMessages.length - 1 : 0;
      logger.log(
        'resyncStateOnHistoryChange',
        `MK (ID: ${firstRecalcId})`,
      );
    } else {
      logger.log('resyncStateOnHistoryChange', ` message_id=${firstRecalcId}`);
    }
  }
  // Case 3:  ()
  else {
    logger.log('resyncStateOnHistoryChange', '');
    // 
    firstRecalcId = oldSelectedMks.length;
    logger.log(
      'resyncStateOnHistoryChange',
      ` (ID: ${firstRecalcId}) `,
    );
  }

  // 3.  MK 
  if (firstRecalcId > -1) {
    const mksToRollback = oldSelectedMks.slice(firstRecalcId).filter(mk => mk) as string[];
    if (mksToRollback.length > 0) {
      logger.log('resyncStateOnHistoryChange', ` ${mksToRollback.length} MK: [${mksToRollback.join(', ')}]`);
      for (const mk of mksToRollback.reverse()) {
        logger.debug('resyncStateOnHistoryChange', `[]  MK: ${mk}`);
        await rollbackByMk(mk, true); // true 
      }
      logger.log('resyncStateOnHistoryChange', '');
    }
  }

  // 4.  selectedMks
  logger.log('resyncStateOnHistoryChange', ` ID ${firstRecalcId} ...`);
  const newSelectedMks: (string | null)[] = oldSelectedMks.slice(0, firstRecalcId); // 

  for (let i = firstRecalcId; i < allMessages.length; i++) {
    const msg = allMessages[i];
    logger.debug('resyncStateOnHistoryChange', `[] : ${i}`);
    const newMk = await ApplyVarChangeForMessage(msg);
    newSelectedMks[i] = newMk; //  message_id ( i) 
  }
  logger.log('resyncStateOnHistoryChange', '');

  // 5.  SelectedMks 
  await updateEraMetaData(meta => {
    _.set(meta, SEL_PATH, newSelectedMks);
    return meta;
  });
  logger.log('resyncStateOnHistoryChange', '');

  // ==================================================================
  //  -  2025/10/02 
  //
  // ****:
  // - swipe 
  //  swipe  MK
  //  MK 
  //  `oldSelectedMks` MK
  // 
  //
  // ****:
  // MK  swipe MK 
  //  swipe 
  //
  //  Bug
  //  `oldSelectedMks`  MK
  //  EditLog
  //  EditLog
  //
  // 
  // ==================================================================
  // logger.log('resyncStateOnHistoryChange', '...');
  // const lastWrittenMk = [...oldSelectedMks].reverse().find(mk => mk);
  // if (lastWrittenMk) {
  //   logger.log('resyncStateOnHistoryChange', `MK: ${lastWrittenMk}...`);
  //   await rollbackByMk(lastWrittenMk, true);
  //   logger.log('resyncStateOnHistoryChange', '...');
  //   await ApplyVarChange(); // ApplyVarChange 
  //   logger.log('resyncStateOnHistoryChange', '');
  // } else {
  //   logger.log('resyncStateOnHistoryChange', 'MK');
  // }
};

